* {
  box-sizing: border-box;
  
}


body {
  background-color: #78a6c9;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2 {
  background: #33678a;
  padding: 20px;
  color: #fff;
  text-align: center;
  font-size: 40px;
}

.App {
  padding: 30px;
  background: #eaeff3;
  width: 80%;
  margin: 80px  auto;
  box-shadow: 1px 2px 5px #eee;
}

input {
  width: 80%;
  border: 0;
  padding: 6px 10px;
  background: #CCC;

}

button {
  width: 20%;
  border: 0;
  padding: 6px 0;
  margin: 0;
  outline: 0;
  color: #FFF;
  font-weight: bold;
  cursor: pointer;
  background: #7aacb3; 
}

ul {
  list-style: none;
  padding: 0;
}

li {
  margin: 15px 0;
}

li span {
  display: inline-block;
  width: 60%;
  color: #6a6a6a;
  overflow: hidden;

}

li > button:first-of-type {
  background: #6a6a6a;
  width: 19%;
  margin-right: 1%;
  color: #FFF;
}

li >button:last-of-type {
  color: #fff;
  background: #e65c4f;
}

ul > form {
  margin: 15px 0;

}

.paragraph{
  text-align: center;
  font-weight: bold;
  color: #e65c4f;
  font-size: 20px;
  padding: 0;
  margin: 0;
}



